* {
  border-color: var(--border);
}
body {
  background: var(--background);
  color: var(--foreground);
  font-family: var(--font-sans);
  -webkit-font-smoothing: antialiased;
  position: relative;
  overflow-x: hidden;
}
body::before {
  content: "";
  position: fixed;
  inset: 0;
  z-index: -2;
  background: radial-gradient(circle at 0% 0%, rgba(30, 41, 59, 1) 0%, transparent 50%),
    radial-gradient(circle at 100% 100%, rgba(15, 23, 42, 1) 0%, transparent 50%),
    radial-gradient(circle at 50% 50%, rgba(2, 6, 23, 1) 0%, transparent 100%);
}
body::after {
  content: "";
  position: fixed;
  inset: 0;
  z-index: -1;
  pointer-events: none;
  background: radial-gradient(circle at -10% -10%, rgba(59, 130, 246, 0.05) 0%, transparent 40%),
    radial-gradient(circle at 110% 110%, rgba(59, 130, 246, 0.05) 0%, transparent 40%);
}
.cyber-grid {
  background-image: linear-gradient(to right, rgba(34, 211, 238, 0.05) 1px, transparent 1px),
    linear-gradient(to bottom, rgba(34, 211, 238, 0.05) 1px, transparent 1px);
  .mask-gradient-v {
    mask-image: linear-gradient(to bottom, transparent, black 10%, black 90%, transparent);
  }
  .mask-gradient-radial {
    mask-image: radial-gradient(circle at center, black, transparent 70%);
  }
  background-size: 40px 40px;
}
.perspective {
  perspective: 2000px;
}
.preserve-3d {
  transform-style: preserve-3d;
}
.backface-hidden {
  backface-visibility: hidden;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: var(--font-heading);
  letter-spacing: -0.02em;
  font-weight: 700;
}
.glass-card {
  background: var(--glass);
  backdrop-filter: blur(12px);
  border: 1px solid var(--glass-border);
  box-shadow: 0 8px 32px 0 rgba(0, 0, 0, 0.3);
}
.text-glow {
  text-shadow: 0 0 20px var(--accent-glow);
}
.text-glow-cyan {
